/* AIZMUGURES BILDE AR GRADIENT */

.background {
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url(../../img/Background1.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
  height: 100vh;
  width: 100%;
}

/* Top Link */

.topline {
  background-color: #3abadf;
  color: white;
  text-align: center;
  padding: 15px 0;
  font-weight: 500;
  line-height: 30px;
  display: flex;
  justify-content: center;
}

.topline_link {
  border-radius: 15px;
  color: #3abadf;
  text-decoration: none;
  background-color: white;
  padding: 5px 15px;
  font-size: 18px;
  transition: all 0.2s ease-in;
  margin-left: 1rem;
}

.topline_link:hover {
  background-color: rgb(216, 216, 216);
}

.topline_link:visited {
  color: #3abadf;
}

/* VISS HEADERIS */
.header {
  display: flex;
  padding: 2rem 3rem;
  padding-top: 5rem;
  justify-content: space-between;
  align-items: center;
  height: 10rem;
}

/* LOGO */
.logo {
  margin: 3rem 0;
}

.logo img {
  height: 150px;
}

/* VISS PAR NAV */
.nav_list {
  list-style-type: none;
  display: flex;
  justify-content: flex-end;
  width: 100%;
}

li {
  align-self: center;
}

.nav_item {
  margin-left: 0.5rem;
  padding: 1rem 0.5rem;
  text-align: center;
}

.nav_item button,
.nav_item a {
  text-decoration: none;
  color: white;
  font-size: 18px;
  font-weight: 600;
  padding: 0.5rem 1rem;
  border-radius: 15px;
  transition: all 0.2s ease;
  background: none;
  align-self: center;
  border: none;
  cursor: pointer;
  width: auto;
}

.nav_item button:hover,
.nav_item a:hover {
  background: #3abadf;
  color: black;
}

.nav_item button:focus,
.nav_item a:focus {
  border: none;
  outline: none;
}

.flags {
  align-self: center;
  display: flex;
  color: white;
  padding: 0rem;
  justify-content: space-evenly;
  align-items: center;
}

/* NAV BEIGAS */

/* HERO VIDUS */
.hero {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 60vh;
  color: white;
  padding-bottom: 2rem;
}

.hero h3 {
  font-size: 72px;
}

.hero h2 {
  font-size: 42px;
  margin-bottom: 2rem;
}

.hero button {
  text-decoration: none;
  color: black;
  font-weight: 600;
  padding: 1.5rem 3rem;
  background-color: #3abadf;

  border-radius: 25px;
  font-size: 24px;
  transition: all 0.1s ease-out;
  border: none;
  cursor: pointer;
}

.hero button:hover {
  background: white;
  color: #3abadf;
}

.hero button:focus {
  border: none;
  outline: none;
}

.burger i {
  display: none;
  color: white;
}

.exit {
  display: none;
}

/* MEDIA QUERIES  */

@media only screen and (max-width: 950px) {
  .background {
    /* background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
      url(../../img/mobile_view.jpg); */
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-attachment: scroll;
    width: 100%;
    height: auto;
    min-height: 600px;
  }

  .logo {
    margin-left: -1rem;
  }

  .logo img {
    height: 140px;
  }

  .hero {
    height: 70vh;
    margin-bottom: 1rem;
  }

  .hero h3 {
    font-size: 48px;
    padding-bottom: 1rem;
  }

  .hero h2 {
    font-size: 32px;
  }

  .hero button {
    font-size: 22px;
    padding: 1.3rem;
  }

  /* Nav List */
  .nav_list {
    position: fixed;
    top: 0;
    left: -100%;
    flex-direction: column;
    justify-content: space-evenly;
    background: rgba(0, 0, 0, 0.86);
    height: 100vh;
    width: 100%;
    margin-top: 0rem;
    padding-top: 2rem;
    padding-bottom: 20vh;
    z-index: 25;
    transition: all 0.5s ease-out;
  }

  .nav_list_active {
    left: 0;
  }

  .nav_list li {
    padding: 1rem 0;
  }

  .nav_list button,
  .nav_list a {
    font-size: 24px;
  }

  /* Viss Par Nav Burger */
  .burger i {
    display: block;
    cursor: pointer;
    color: white;
    font-size: 36px;
  }

  .exit {
    color: white;
    cursor: pointer;
    font-size: 36px;
    display: none;
    position: fixed;
    top: 8vh;
    right: 3rem;
    z-index: 26;
    transition: all ease-out 0.5s;
  }

  .active {
    display: block;
  }

  .hide {
    display: none;
  }

  /* Beigas par Nav Burger */
  /* Par karodziniem */
  .flag {
    margin: 0 1rem;
  }
}

@media only screen and (max-width: 950px) {
  ul {
    width: 30%;
  }
}

@media only screen and (max-width: 650px) {
  .topline {
    flex-direction: column;
  }

  .topline_link {
    margin: 0;
  }
}
