.white_wave {
  background: url(../../img/WhiteWave.png);
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  width: 100%;
  height: 20vh;
  left: 0px;
  top: 90vh;
}

@media only screen and (max-width: 1050px) {
  .white_wave {
    top: 90vh;
  }
}
@media only screen and (max-width: 500px) {
  .white_wave {
    top: 94vh;
  }
}
